import React, { createContext, useEffect, useReducer } from "react";
import "./App.css";
import BotsFilters from "./components/BotsFilters/BotsFilters";
import { BotsList } from "./components/BotsList/BotsList";
import ErrorsAlerts from "./components/ErrorsAlerts/ErrorsAlerts";
import { getAccounts } from "./utils/ApiUtils";

const initialFiltersState = {
  scope: "",
  type: "",
  account: "",
  sorting: "created_at-desc",
};

const initialAccountsState: any = {
  accounts: [],
};

const initialErrorsState: any = {
  errors: [],
};

function errorsReducer(state = initialErrorsState, action: any) {
  switch (action.type) {
    case "addError":
      const errors = new Set(state.errors);
      errors.add(action.payload);
      return { errors: [...Array.from(errors)] };
    case "cleanErrors":
      return { errors: [] };
    default:
      throw new Error();
  }
}

function accountsReducer(state = initialAccountsState, action: any) {
  switch (action.type) {
    case "setAccounts":
      return { accounts: [...state.accounts, ...action.payload] };
    default:
      throw new Error();
  }
}

function filtersReducer(state = initialFiltersState, action: any) {
  switch (action.type) {
    case "setScope":
      return { ...state, scope: action.payload };
    case "setAccount":
      return { ...state, account: action.payload };
    case "setType":
      return { ...state, type: action.payload };
    case "setSorting":
      return { ...state, sorting: action.payload };
    default:
      throw new Error();
  }
}

export const FiltersContext = createContext({
  filtersState: initialFiltersState,
  filtersDispatch: (state: any) => {},
});

export const AccountsContext = createContext({
  accountsState: initialAccountsState,
  accountsDispatch: (state: any) => {},
});

export const ErrorsContext = createContext({
  errorsState: initialErrorsState,
  errorsDispatch: (state: any) => {},
});

function App() {
  const [filtersState, filtersDispatch] = useReducer(
    filtersReducer,
    initialFiltersState
  );
  const [accountsState, accountsDispatch] = useReducer(
    accountsReducer,
    initialAccountsState
  );
  const [errorsState, errorsDispatch] = useReducer(
    errorsReducer,
    initialErrorsState
  );

  const getAccountData = async () => {
    try {
      const data = await getAccounts();
      accountsDispatch({ type: "setAccounts", payload: data });
    } catch {
      accountsDispatch({ type: "setAccounts", payload: [] });
      errorsDispatch({
        type: "addError",
        payload: "Error when fetching accounts list",
      });
    }
  };

  useEffect(() => {
    getAccountData();
  }, []);

  return (
    <ErrorsContext.Provider value={{ errorsState, errorsDispatch }}>
      <AccountsContext.Provider value={{ accountsState, accountsDispatch }}>
        <FiltersContext.Provider value={{ filtersState, filtersDispatch }}>
          <div className="App">
            <BotsFilters />
            <BotsList />
            <ErrorsAlerts />
          </div>
        </FiltersContext.Provider>
      </AccountsContext.Provider>
    </ErrorsContext.Provider>
  );
}

export default App;
